import React from "react";
import "./Icon.less";
import HeartIconUrl from "./svg/ic-heart-bk.svg";
import RecIconUrl from "./svg/ic-rec-bk.svg";
import RecWhiteIconUrl from "./svg/ic-rec-white.svg";
import ShareIconUrl from "./svg/ic-share-bk.svg";

interface Props {
    type: "heart" | "rec" | "share" | "rec-white";
    onClick?: () => void;
}

interface States {

}

export default class Icon extends React.Component<Props, States> {
    handleOnClick = () => {
        this.props.onClick && this.props.onClick();
    };

    render() {
        const { type } = this.props;
        return (
            <button className={"icon"} onClick={this.handleOnClick}>
                { type === "heart" && <img src={HeartIconUrl} alt={type} /> }
                { type === "rec" && <img src={RecIconUrl} alt={type} /> }
                { type === "rec-white" && <img src={RecWhiteIconUrl} alt={type} /> }
                { type === "share" && <img src={ShareIconUrl} alt={type} /> }
            </button>
        );
    }
}
