import React from "react";
import { PostAuthor } from "@Types/model/Posts";
import Icon from "@Components/icon/Icon";
import ReactGa from "react-ga";

interface Props {
    author: PostAuthor;
    vendorType: string;
    postType?: string;
}

interface State {

}

export default class Profile extends React.Component<Props, State> {
    sendEmail(type: string): void {
        ReactGa.event({
            category: "social-button",
            action: `${type}-click`,
            label: "social"
        });
        window.open("https://forms.gle/A2CK2iEV3NgSjA4d7", "_blank");
    }
    render() {
        const { author, vendorType, postType } = this.props;
        return (
            <footer className={"profile"}>
                <div className={"profile-image"}>
                    <div>
                        <img src={author.profileImageUrl} />
                    </div>
                    <div className={"vendor-image"}>
                        {
                            vendorType === "TIKTOK" &&
                                <img src="/images/logo_tiktok@3x.png" alt={vendorType} />
                        }
                        {
                            vendorType === "INSTAGRAM" &&
                            <img src="/images/logo_insta@3x.png" alt={vendorType} />
                        }
                    </div>
                </div>
                <div className={"profile-desc"}>
                    <div className={"profile-name"}>{author.name}</div>
                    <div className={"post-date"}>2020.02.02</div>
                </div>
                <div className={"profile-button"}>
                    <Icon type={"heart"} onClick={() => this.sendEmail("heart")}/>
                    <Icon type={"share"} onClick={() => this.sendEmail("share")} />
                    {/*<Icon type={postType === "overlay" ? "rec-white" : "rec"} onClick={() => this.sendEmail("rec")} />*/}
                </div>
            </footer>
        )
    }
}
